// 제목 폰트
.akaya-kanadaka-regular {
  font-family: "Akaya Kanadaka", "system-ui";
  font-weight: 400;
  font-style: normal;
}
.salsa-regular {
  font-family: "Salsa", "cursive";
  font-weight: 400;
  font-style: normal;
}
// 내용 폰트
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
.gowun-dodum-regular {
  font-family: "Gowun Dodum", "sans-serif";
  font-weight: 400;
  font-style: normal;
}
.gowun-batang-regular {
  font-family: "Gowun Batang", "serif";
  font-weight: 400;
  font-style: normal;
}
.noto-serif-kr-regular {
  font-family: "Noto Serif KR", "serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
.noto-sans-kr-regular {
  font-family: "Noto Sans KR", "sans-serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}


$pointColor: #41A8BF;
$bgColor: #FDFDFD;

.About {
  display: flex;
  width: 100%;
  // height: calc(100vh - 196px);

  .side {
    width: 280px;
    height: calc(100% - 196px);
    border-right: 2px solid $pointColor;
    position: fixed;
    top: 176px;
    left: 22px;
    z-index: 80;

    ul {
      display: flex;
      flex-direction: column;
      align-items: center;
      line-height: 30px;
      margin: 93.2% 0;


      li {
        cursor: pointer;
        font-family: "Akaya Kanadaka", "Salsa";
        font-size: 1.3em;
        color: $pointColor;
      }
    }
  }

  .content {
    width: 100%;
    height: calc(100vh - 196px);
    margin-left: 280px;
    // background-color: aquamarine;
    overflow-y: auto;

    &::-webkit-scrollbar {
      display: none;
    }

    .about_tit {
      text-align: center;
      width: 100%;
      line-height: 78px;
      font-family: "Akaya Kanadaka", "Salsa";
      font-size: 1.3em;
      color: $pointColor;
      border-bottom: 2px solid $pointColor;

      &:nth-of-type(2) {border-top: 2px solid $pointColor;}
    }

    .skills {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      // height: 400px;

      > li {
        width: 16.666666666666666666666666666667%;
        padding: 20px;
        color: $pointColor;
        text-align: center;

        @for $i from 1 through 6 {
          &:nth-of-type(#{$i}) {
            border-bottom: 2px solid $pointColor;
          }
        }

        &:not(:nth-of-type(6), :nth-of-type(12)) {
          border-right: 2px solid $pointColor;
        }

        &:last-child {
          background-color: $pointColor;
        }

        .my_skill {
          fill: $pointColor;
        }
      }
    }
  }
}